$templateCache.put('views/directives/tasks/framing-task.html','<div class="framing-task">\n\n  <p>In dieser \xDCbung gibt es zwei Gewinnspiele. Du kannst nun entscheiden, an welchen Gewinnspielen du teilnehmen w\xFCrdest (du kannst also an keinem, einem oder an beiden Gewinnspielen teilnehmen).</p>\n\n  <div\n    data-uk-grid=""\n    class="uk-grid-match uk-child-width-1-2@s">\n\n    <div\n      data-card=""\n      data-card-disabled="framingTaskController.resolved"\n      data-card-on-click="framingTaskController.update(data, selected)">\n      <card-title>\n        Gewinnspiel A\n      </card-title>\n      <card-text>\n        Bei diesem Gewinnspiel kannst du mit 10%-iger Wahrscheinlichkeit 95 Euro gewinnen und mit 90%-iger Wahrscheinlichkeit 5 Euro verlieren.\n      </card-text>\n\n      <card-buttons>\n        <div class="uk-margin-top">\n          <button\n            class="uk-button uk-button-small"\n            data-ng-class="{\n              \'uk-button-primary\': framingTaskController.lotteryA === true,\n              \'uk-button-default\': framingTaskController.lotteryA !== true\n            }"\n            data-ng-disabled="framingTaskController.resolved"\n            data-ng-click="framingTaskController.update(\'lotteryA\', true)">\n            Ja\n          </button>\n          <button\n            class="uk-button uk-button-small"\n            data-ng-class="{\n              \'uk-button-primary\': framingTaskController.lotteryA === false,\n              \'uk-button-default\': framingTaskController.lotteryA !== false\n            }"\n            data-ng-disabled="framingTaskController.resolved"\n            data-ng-click="framingTaskController.update(\'lotteryA\', false)">\n            Nein\n          </button>\n        </div>\n      </card-buttons>\n    </div>\n\n    <div\n      data-card=""\n      data-card-disabled="framingTaskController.resolved"\n      data-card-on-click="framingTaskController.update(data, selected)">\n      <card-title>\n        Gewinnspiel B\n      </card-title>\n      <card-text>\n        Die Teilnahme diesem Gewinnspiel kostet 5 Euro, wobei du anschlie\xDFend mit 10%-iger Wahrscheinlichkeit 100 Euro gewinnen kannst.\n      </card-text>\n      <card-buttons>\n        <div class="uk-margin-top">\n          <button\n            class="uk-button uk-button-small"\n            data-ng-class="{\n              \'uk-button-primary\': framingTaskController.lotteryB === true,\n              \'uk-button-default\': framingTaskController.lotteryB !== true\n            }"\n            data-ng-disabled="framingTaskController.resolved"\n            data-ng-click="framingTaskController.update(\'lotteryB\', true)">\n            Ja\n          </button>\n          <button\n            class="uk-button uk-button-small"\n            data-ng-class="{\n              \'uk-button-primary\': framingTaskController.lotteryB === false,\n              \'uk-button-default\': framingTaskController.lotteryB !== false\n            }"\n            data-ng-disabled="framingTaskController.resolved"\n            data-ng-click="framingTaskController.update(\'lotteryB\', false)">\n            Nein\n          </button>\n        </div>\n      </card-buttons>\n    </div>\n  </div>\n\n  <div data-evaluation="framingTaskController">\n    <evaluation-text>\n      <p>\n        <strong>\n          <span class="uk-text-primary" data-ng-if="framingTaskController.lotteryA === true && framingTaskController.lotteryB === true">\n            Du hast dich entschieden, an beiden Gewinnspielen teilzunehmen. Lies weiter, um mehr \xFCber diese \xDCbung zu erfahren.\n          </span>\n          <span class="uk-text-primary" data-ng-if="framingTaskController.lotteryA === false && framingTaskController.lotteryB === false">\n            Du hast dich entschieden, an keinem der beiden Gewinnspiele teilzunehmen. Lies weiter, um mehr \xFCber diese \xDCbung zu erfahren.\n          </span>\n          <span class="uk-text-danger" data-ng-if="(\n            (framingTaskController.lotteryA === true && framingTaskController.lotteryB === false) ||\n            (framingTaskController.lotteryA === false && framingTaskController.lotteryB === true)\n          )">\n            <span>Du hast dich entschieden, an Gewinnspiel </span>\n            <span data-ng-if="framingTaskController.lotteryA">A</span>\n            <span data-ng-if="framingTaskController.lotteryB">B</span>\n            <span> teilzunehmen, nicht aber an Gewinnspiel </span>\n            <span data-ng-if="!framingTaskController.lotteryA">A</span>\n            <span data-ng-if="!framingTaskController.lotteryB">B</span>\n            <span>. Du bist daher in die sogenannte "Framing"-Falle getappt. Lies weiter, um mehr dar\xFCber zu erfahren.</span>\n          </span>\n        </strong>\n      </p>\n\n      <p>Studien haben gezeigt, dass die <strong>meisten Menschen Gewinnspiel A ablehnen</strong>, bei <strong>Gewinnspiel B jedoch mitmachen</strong>. Bei genauem Hinsehen sind die beiden Gewinnspiele aber v\xF6llig identisch: Mit 10%-iger Wahrscheinlichkeit bekommst du 95 Euro, w\xE4hrend du mit 90%-iger Wahrscheinlichkeit 5 Euro verlierst.</p>\n\n      <p>Bei <strong>Gewinnspiel A</strong> geht dies offensichtlich aus der Beschreibung hervor.</p>\n      <p>Bei <strong>Gewinnspiel B</strong> ist davor eine kleine Rechnung n\xF6tig: Dadurch, dass du 5 Euro bezahlen musst, bevor du an dem Gewinnspiel teilnehmen kannst, verringert sich deine Auszahlung in jedem Fall um 5 Euro. Falls du gewinnst, bleiben dir also 100 \u2013 5 = 95 Euro. Wenn du nicht gewinnst, hast du am Ende 5 Euro verloren (den Einsatz f\xFCr die Lotterie). Wie bei Gewinnspiel A kannst du also mit 10-%iger Wahrscheinlichkeit 95 Euro gewinnen, verlierst aber mit 90%-iger Wahrscheinlichkeit 5 Euro.</p>\n\n      <p>Die beiden Gewinnspiele wurden also <strong>zwar unterschiedlich beschrieben</strong>, sind im Endeffekt <strong>aber identisch</strong>. Wie du vielleicht selbst bei dir festgestellt hast, kann die Art und Weise, wie eine Situation beschrieben wird, zu unterschiedlichen Entscheidungen f\xFChren. Dieser Effekt wird <strong>"Framing"</strong> genannt: Durch die unterschiedliche Beschreibung verschiebt sich der Rahmen (Englisch "frame"), in dem wir die Entscheidungsalternativen interpretieren.</p>\n\n      <p>Besonders stark wirkt dieser Effekt, wenn <strong>bestimmte Resultate als positiv</strong> (Gewinn, Nutzen) und <strong>andere Resultate als negativ</strong> (Verlust, Schaden) dargestellt werden. Da die meisten Menschen vor Verlusten zur\xFCckschrecken, w\xE4hlen sie lieber jene Entscheidungen, die gef\xFChlt weniger Verluste bringen und mehr Gewinne bringen (so wie Gewinnspiel B beim obigen Beispiel). Oft wird Framing auch bei Lotterien oder Gewinnspielen eingesetzt: Hier werden einzelne Gewinne \xFCberbetont, sodass Menschen zur Teilnahme angeregt werden, obwohl Lotterien und Gewinnspiele in der Regel ein Verlustgesch\xE4ft f\xFCr die Spieler darstellen.</p>\n\n      <p><strong>Beispiel:</strong> Stell dir vor, du sollst eine Sportveranstaltung organisieren. F\xFCr die Planung der Veranstaltung ist es sehr wichtig, ausreichend fr\xFCh zu wissen, wie viele Teilnehmer kommen werden. Deshalb setzt du eine Frist f\xFCr die Anmeldung. Was aber, wenn sich die Leute nicht daran halten und sich zu sp\xE4t anmelden? Eine harte M\xF6glichkeit ist nat\xFCrlich, alle versp\xE4teten Abmeldungen abzuweisen. Es gibt aber noch einen anderen Weg, die Teilnehmer zu motivieren, sich fr\xFChzeitig anzumelden: Du kannst f\xFCr rechtzeitige und versp\xE4tete Anmeldungen unterschiedliche Geb\xFChren verlangen.</p>\n\n      <p>Diese unterschiedlichen Geb\xFChren kannst du auf zwei Arten "framen": Einerseits als Rabatt f\xFCr rechtzeitige Anmeldung (positive Formulierung), oder andererseits als Zusatzgeb\xFChr f\xFCr versp\xE4tete Anmeldungen (negative Formulierung). Beides l\xE4uft letztlich auf das Gleiche hinaus: Diejenigen, die sich rechtzeitig anmelden, zahlen einen geringeren Betrag als diejenigen, die sich zu sp\xE4t anmelden. Die Situation wird nur jeweils anders benannt und dargestellt. So wie bei der \xDCbung mit den Gewinnspielen zeigt sich in vielen Studien, dass das <strong>negative Framing</strong> "Zusatzgeb\xFChren f\xFCr versp\xE4tete Anmeldung" <strong>st\xE4rker wirkt als das positive Framing</strong> "Rabatt f\xFCr rechtzeitige Anmeldung".</p>\n\n      <p>Auch <strong>im allt\xE4glichen Konsum</strong> begegnet uns dieses Ph\xE4nomen: So wird beispielsweise bei "Light"-Produkten oft angegeben, um wieviel weniger Fett sich in diesen Produkten im Vergleich zu anderen befindet: "50% weniger Fett als bei herk\xF6mmlicher Mayonnaise" h\xF6rt sich gut an. W\xFCrde hingegen der Fettanteil selbst angegeben ("Der Fettanteil bei dieser Mayonnaise betr\xE4gt 25%""), so klingt das eher negativ und w\xFCrde m\xF6glicherweise vom Kauf abschrecken.</p>\n\n      <p>Framing kann sowohl bewusst als auch unbewusst stattfinden, sodass viele unserer Entscheidungen diesem Ph\xE4nomen unterliegen. Umso wichtiger ist es daher, <strong>sich dieser Tatsache bewusst zu sein</strong> und immer wieder zu \xFCberlegen, ob wir bei einer Finanz- oder Konsumentscheidung nicht m\xF6glicherweise durch einen Framing-Effekt getrieben werden.</p>\n\n    </evaluation-text>\n  </div>\n\n</div>\n');
