$templateCache.put('views/directives/tasks/interest-task.html','<div class="interest-task">\n\n  <div class="description">\n    <p>Wir haben f\xFCr dich {{interestTaskController.amount|currency:\' Euro\':0}} auf dein Spielgeld-Sparbuch einbezahlt. Die Bank bezahlt dir {{interestTaskController.rate|percent}} pro Jahr.</p>\n  </div>\n\n  <div class="question-1 question">\n      <p><strong>Frage 1:</strong> Wie viel Geld befindet sich in einem Jahr auf dem Sparbuch? Ziehe die entsprechenden Scheine und M\xFCnzen in den Geldbeutel!</p>\n  </div>\n\n  <div\n    class="exercise-1 exercise uk-inline"\n    data-ng-class="{\n      \'disabled\': interestTaskController.resolved\n    }"\n    data-interest-task-exercise=""\n    data-interest-task-exercise-disabled="interestTaskController.resolved"\n    data-interest-task-exercise-sum="interestTaskController.exercise1Answer"\n    data-interest-task-exercise-on-update="interestTaskController.update(sum, \'exercise1\')">\n  </div>\n\n  <hr class="uk-margin-medium">\n\n  <div class="question-2 question">\n    <p>\n      <strong>Frage 2:</strong>\n      <span>Wenn du das Geld</span>\n      <span data-ng-if="interestTaskController.years === 1"> ein weiteres Jahr </span>\n      <span data-ng-if="interestTaskController.years !== 1"> {{interestTaskController.years}} weitere Jahre </span>\n      <span>auf dem Sparbuch liegen l\xE4sst, wieviel Geld befindet sich dann auf dem Sparbuch (also nach insgesamt {{interestTaskController.years + 1}} Jahren)? Ziehe wieder die entsprechenden Scheine und M\xFCnzen in den Geldbeutel!</span>\n    </p>\n  </div>\n\n  <div\n    class="exercise-2 exercise uk-inline"\n    data-ng-class="{\n      \'disabled\': interestTaskController.resolved\n    }"\n    data-interest-task-exercise=""\n    data-interest-task-exercise-disabled="interestTaskController.resolved"\n    data-interest-task-exercise-sum="interestTaskController.exercise2Answer"\n    data-interest-task-exercise-on-update="interestTaskController.update(sum, \'exercise2\')">\n  </div>\n\n  <div data-evaluation="interestTaskController">\n    <evaluation-text>\n      <p data-ng-class="{\n          \'uk-text-success\': interestTaskController.correctAnswers === 2,\n          \'uk-text-warning\': interestTaskController.correctAnswers === 1,\n          \'uk-text-danger\': interestTaskController.correctAnswers === 0\n        }">\n        <strong>\n          <span data-ng-if="interestTaskController.correctAnswers === 2">\n            Herzlichen Gl\xFCckwunsch, du hast beide Aufgaben richtig gel\xF6st.\n          </span>\n          <span data-ng-if="interestTaskController.correctAnswers === 1">\n            <span data-ng-if="interestTaskController.exercise1Correct">Super, du hast Aufgabe 1 richtig gel\xF6st. Aufgabe 2 hast du leider falsch gel\xF6st. </span>\n            <span data-ng-if="interestTaskController.exercise2Correct">Super, du hast Aufgabe 2 richtig gel\xF6st. Aufgabe 1 hast du leider falsch gel\xF6st. </span>\n            <span>Die folgende Erkl\xE4rung hilft dir dabei, den Zinseffekt besser zu verstehen.</span>\n          </span>\n          <span data-ng-if="interestTaskController.correctAnswers === 0">\n            Leider hast du beide Aufgaben falsch gel\xF6st. Die folgende Erkl\xE4rung hilft dir dabei, den Zinseffekt besser zu verstehen.\n          </span>\n        </strong>\n      </p>\n\n      <p>Zinsen werden meistens in Prozent angegeben und ha\u0308ngen somit von der Ho\u0308he des u\u0308berlassenen Geldbetrages ab. Je mehr Geld du also auf dem Sparbuch hast, umso ho\u0308her sind auch die Zinsen, die du dafu\u0308r bekommst.</p>\n\n      <p>Angenommen, du hast zu Beginn 1.000 Euro auf deinem Sparbuch und erha\u0308ltst im ersten Jahr 2% Zinsen auf 1.000 Euro, also</p>\n      <p><code>0,02 x 1.000 = 20 Euro</code></p>\n      <p>Dein Guthaben betra\u0308gt in einem Jahr daher</p>\n      <p><code>1.000 + 20 = 1.020 Euro</code><br>(Startguthaben + Zinsen aus dem ersten Jahr)</p>\n      <p>Wenn du das Geld nun ein weiteres Jahr auf dem Sparbuch la\u0308sst, erha\u0308ltst du im zweiten Jahr 2% Zinsen auf dein neues Guthaben von 1.020 Euro, die Zinsen im zweiten Jahr betragen demnach</p>\n      <p><code>0,02 x 1.020 = 20,40 Euro</code></p>\n      <p>Weshalb sind die Zinsen im zweiten Jahr ho\u0308her als im ersten Jahr?</p>\n\n      <p>Das liegt daran, dass dein Guthaben im ersten Jahr durch die Zinsen des ersten Jahres um 20 Euro gewachsen ist. Im zweiten Jahr erha\u0308ltst du nun nicht nur Zinsen auf die urspru\u0308nglichen 1.000 Euro, sondern auf das neue Guthaben von 1.020 Euro. Deshalb erha\u0308ltst du im zweiten Jahr 20,40 Euro an Zinsen, sodass sich nach zwei Jahren</p>\n      <p><code>1.020 + 20,40 = 1.040,40 Euro</code><br>(Guthaben nach einem Jahr + Zinsen aus dem zweiten Jahr)</p>\n      <p>auf deinem Sparbuch befinden. Du bekommst also im zweiten Jahr auch Zinsen auf die bereits erhaltenen Zinsen aus dem ersten Jahr, was als <strong>"Zinseszinsen"</strong> bezeichnet wird. Durch die Zinseszinsen wa\u0308chst dein Guthaben daher immer schneller, je la\u0308nger du es auf dem Sparbuch la\u0308sst. Dieser Effekt wirkt anfangs eher klein, kann u\u0308ber die Jahre aber gro\xDFe Auswirkungen haben.</p>\n    </evaluation-text>\n\n  </div>\n\n</div>\n');
$templateCache.put('views/directives/tasks/mental-bookkeeping-task.html','<div class="anchoring-task">\n\n  <div data-ng-switch="mentalBookkeepingTaskController.group">\n\n    <div data-ng-switch-when="GROUP_A">\n      <div class="description">\n        <p>Du willst dir ein Konzertticket um 20 Euro kaufen. Am Eingang angekommen siehst du, dass du auf dem Weg 20 Euro verloren hast. Kaufst du dir ein Ticket (du hast noch genug Geld dabei)?</p>\n      </div>\n\n      <div\n        data-uk-grid=""\n        data-card-container=""\n        class="uk-grid-match uk-child-width-1-2@s">\n\n        <div\n          data-card=""\n          data-card-data="1"\n          data-card-is-checkbox="true"\n          data-card-disabled="mentalBookkeepingTaskController.resolved"\n          data-card-on-click="mentalBookkeepingTaskController.update(data, selected)">\n          <card-title>\n            Option 1\n          </card-title>\n          <card-text>\n            Ja, du kaufst das Ticket.\n          </card-text>\n          <card-buttons>\n          </card-buttons>\n        </div>\n\n        <div\n          data-card=""\n          data-card-data="2"\n          data-card-is-checkbox="true"\n          data-card-disabled="mentalBookkeepingTaskController.resolved"\n          data-card-on-click="mentalBookkeepingTaskController.update(data, selected)">\n          <card-title>\n            Option 2\n          </card-title>\n          <card-text>\n            Nein, du kaufst das Ticket nicht.\n          </card-text>\n          <card-buttons>\n          </card-buttons>\n        </div>\n\n      </div>\n    </div>\n\n    <div data-ng-switch-when="GROUP_B">\n\n      <div class="description">\n        <p>Du hast dir ein Konzertticket um 20 Euro gekauft. Am Eingang angekommen siehst du, dass du das Ticket verloren hast. Kaufst du dir dort erneut ein Ticket (du hast noch genug Geld dabei)?</p>\n      </div>\n\n      <div\n        data-uk-grid=""\n        data-card-container=""\n        class="uk-grid-match uk-child-width-1-2@s">\n\n        <div\n          data-card=""\n          data-card-data="1"\n          data-card-is-checkbox="true"\n          data-card-disabled="mentalBookkeepingTaskController.resolved"\n          data-card-on-click="mentalBookkeepingTaskController.update(data, selected)">\n          <card-title>\n            Option 1\n          </card-title>\n          <card-text>\n            Ja, du kaufst das Ticket.\n          </card-text>\n          <card-buttons>\n          </card-buttons>\n        </div>\n\n        <div\n          data-card=""\n          data-card-data="2"\n          data-card-is-checkbox="true"\n          data-card-disabled="mentalBookkeepingTaskController.resolved"\n          data-card-on-click="mentalBookkeepingTaskController.update(data, selected)">\n          <card-title>\n            Option 2\n          </card-title>\n          <card-text>\n            Nein, du kaufst das Ticket nicht.\n          </card-text>\n          <card-buttons>\n          </card-buttons>\n        </div>\n\n      </div>\n\n    </div>\n\n    <div data-ng-switch-default="">\n      <div data-uk-alert="" class="uk-alert-primary">\n        Die Aufgabe ist nur f\xFCr Sch\xFCler sichtbar.\n      </div>\n    </div>\n\n  </div>\n\n  <div data-evaluation="mentalBookkeepingTaskController">\n    <evaluation-text>\n      <p>Wie so oft bei <strong>pers\xF6nlichen Entscheidungen</strong> gibt es auch hier <strong>kein "richtig" oder "falsch"</strong>. Dass sich viele Menschen in den beiden oben beschriebenen Situationen anders verhalten w\xFCrden, ist auf die sogenannte <strong>"mentale Buchf\xFChrung"</strong> (also gedachte Buchf\xFChrung) zur\xFCckzuf\xFChren. Den \xDCberblick bewahren bedeutet, dass wir <strong>unser Geld im Kopf auf bestimmte Konten verteilen</strong> \u2013 unabh\xE4ngig davon, ob es diese verschiedenen Konten auch in der Wirklichkeit gibt. Die Einteilung erfolgt dabei nach unterschiedlichen Ma\xDFst\xE4ben, wie etwa der Herkunft des Geldes, dem Verwendungszweck oder der Frage, ob das Geld mit anderen Menschen geteilt wird.</p>\n\n      <p>Was passiert in unserem Beispiel? Die meisten Menschen w\xFCrden in beiden Situationen ein mentales Konto "Ticketkauf" anlegen und diesem Konto 20 Euro zuweisen. Im Text der ersten Gruppe verlieren wir 20 Euro, das Konto "Ticketkauf" ist davon aber nicht betroffen \u2013 hier befinden sich immer noch 20 Euro. Deshalb entscheiden sich viele daf\xFCr, das Ticket trotzdem zu kaufen. Im Text der zweiten Gruppe hingegen haben wir die 20 Euro auf dem Konto "Ticketkauf" bereits verbraucht \u2013 das Konto ist daher leer. Bei strikter mentaler Buchf\xFChrung k\xF6nnen wir uns daher kein neues Ticket kaufen. Aus finanzieller Sicht sind beide Situationen jedoch gleich: 20 Euro sind verloren, egal ob als Geldschein oder als Ticket. Das Beispiel zeigt, dass die Einteilung in mentale Konten eine direkte Auswirkung auf unser Verhalten haben kann.</p>\n\n      </p>Den \xDCberblick bewahren <strong>hilft uns dabei, den \xDCberblick \xFCber unsere Einnahmen und Ausgaben zu behalten</strong>. Manchmal kann sie aber auch die Sicht auf kluge Entscheidungen verstellen.</p>\n\n      <p><strong>Beispiel 1:</strong> Laura hat sich vor einiger Zeit ein eigenes Auto gekauft. Sie f\xFChrt ein mentales Konto f\xFCr ihre Tankkosten und m\xF6chte die Ausgaben auf diesem Konto m\xF6glichst gering halten. F\xFCr die Autofahrten selbst f\xFChrt sie ein getrenntes mentales Konto. Da die Tankstelle im 10 Kilometer entfernten Nachbarort Benzin etwas billiger anbietet, f\xE4hrt sie einmal im Monat dorthin, um zu tanken. Durch die getrennten mentalen Konten \xFCbersieht sie jedoch, dass die Autofahrt in den Nachbarort und zur\xFCck mehr kostet, als die Ersparnis beim Tanken ausmacht (ganz abgesehen von der Zeit, die sich durch den zus\xE4tzlichen Weg verliert).</p>\n\n      <p><strong>Beispiel 2:</strong> Felix hat f\xFCr eine neue Wohnung einen Kredit aufgenommen und f\xFChrt nun ein mentales Konto f\xFCr seine Schulden und die anfallenden Zinsen in H\xF6he von 3% j\xE4hrlich. Auf der anderen Seite f\xFChrt er ein mentales Konto f\xFCr seine Ersparnisse, das er auch real angelegt hat. Auf diesem Sparkonto (siehe auch \xDCbung 3.3) erh\xE4lt er 0,2% Zinsen j\xE4hrlich. Durch eine unerwartete Erbschaft bekommt Felix 10.000 Euro, die er gleich auf seinem Konto f\xFCr Ersparnisse verbucht. Leider f\xE4llt ihm durch diese mentale Buchf\xFChrung nicht auf, dass es viel besser f\xFCr ihn w\xE4re, mit der Erbschaft einen Teil seiner Schulden zur\xFCckzuzahlen, da die Zinsen f\xFCr den Kredit viel h\xF6her sind als die f\xFCr seine Ersparnisse. Durch die R\xFCckzahlung der Schulden w\xFCrde er sich jedes Jahr ein Vielfaches von dem an Zinsen sparen, was er auf dem Sparkonto erh\xE4lt.</p>\n\n      <p>Viele Finanzexperten empfehlen, einen <strong>gewissen finanziellen Polster f\xFCr Notf\xE4lle</strong> anzusparen, sagen aber, dass es dar\xFCber hinaus die <strong>beste Form des Sparens</strong> ist, seine <strong>Schulden zu verringern</strong>.</p>\n    </evaluation-text>\n  </div>\n\n</div>\n');
