$templateCache.put('views/directives/tasks/self-commitment-task.html','<div class="self-commitment">\n\n  <div class="uk-placeholder">VIDEO #1</div>\n\n  <div class="uk-placeholder">VIDEO #2</div>\n\n  <p>Es ist nicht immer leicht, seine <strong>Ziele konsequent zu verfolgen</strong>. Oft kommen kleinere und gr\xF6\xDFere Versuchungen dazwischen, die uns von unseren Zielen abbringen. Es gibt zum Gl\xFCck aber ein paar einfache Tricks, wie man sich selber "\xFCberlisten" kann.</p>\n\n  <p>Wir k\xF6nnen es \xE4hnlich machen, wie der <strong>Seefahrer Odysseus</strong> in einer alten griechischen Erz\xE4hlung. Er musste auf seinem Weg nach Hause an einer Insel vorbei segeln, auf der die Sirenen lebten: Sch\xF6ne Frauen, die bezaubernd singen konnten. Mit ihrem Gesang lockten sie Seefahrer an, um sie dann zu t\xF6ten. Odysseus wusste das und lie\xDF sich an der Vor\xFCberfahrt an den Mast binden. So konnte er der Verlockung nicht nachgeben und unbeirrt nach Hause segeln.</p>\n  <p>Diese Idee von Odysseus, <strong>sich selbst zu binden, um ein Ziel zu erreichen</strong>, k\xF6nnen wir in vielen Situationen n\xFCtzen. Wenn du beispielsweise Schwierigkeiten hast, dich zum Lernen zu motivieren, kannst du mit deinen Freunden einen fixen Termin ausmachen, an dem ihr gemeinsam lernt.</p>\n  <p>Welche M\xF6glichkeiten gibt es, diese Strategie auch f\xFCr unsere finanziellen Ziele einzusetzen?</p>\n  <p>Lukas hat zu seinem 15. Geburtstag 100 Euro von seinen Gro\xDFeltern bekommen. Er m\xF6chte das Geld f\xFCr den F\xFChrerschein sparen, bef\xFCrchtet aber, dass er es bereits fr\xFCher f\xFCr andere Dinge ausgeben wird. Da erinnert sich Lukas an die Geschichte von Odysseus und legt das Geld auf ein <strong>Kapitalsparbuch mit dreij\xE4hriger Bindung</strong>, bei dem er h\xF6here Zinsen als auf einem ungebundenen Sparbuch erh\xE4lt.</p>\n  <p>Auch Hannah will f\xFCr ihren F\xFChrerschein sparen und daf\xFCr jeden Monat 10 Euro auf die Seite legen. Leider hat dieser Plan bisher nicht so gut funktioniert, da sie immer wieder zu viel ausgibt. Darum er\xF6ffnet sie ein <strong>Pr\xE4miensparbuch mit dreij\xE4hriger Laufzeit</strong> und vereinbart mit der Bank, monatlich 10 Euro einzuzahlen.  Hannah erh\xE4lt daf\xFCr ebenfalls h\xF6here Zinsen.</p>\n  <p>Lukas und Hannah haben sich also durch ihre Vereinbarungen mit der Bank gebunden, tats\xE4chlich zu sparen. Nur wenn sie das Geld nicht vorzeitig beheben, bekommen sie die h\xF6heren Zinsen.</p>\n  <p>Einen <strong>Mittelweg bildet das Sparkonto ohne Bindung</strong>, das Banken oft zus\xE4tzlich zum Girokonto anbieten. Geld, das gespart werden soll, kann dann vom Girokonto auf das Sparkonto \xFCbertragen werden, welches h\xF6here Zinsen bietet. Bei den meisten Banken muss Geld vom Sparkonto zun\xE4chst wieder auf das Girokonto \xFCbertragen werden, bevor es behoben werden kann. Mit diesem zus\xE4tzlichen Schritt haben wir Zeit, erst zu \xFCberlegen, ob wir das Geld auch wirklich ausgeben wollen, und k\xF6nnen so unseren kurzfristigen Impulsen leichter widerstehen.</p>\n  <p>Das <strong>Konzept der Selbstbindung</strong> funktioniert auch bei einer Vielzahl von anderen Gelegenheiten. So  k\xF6nnen wir beispielsweise \xFCber Wertkarten (statt eines Tarifvertrags) festlegen, wie viel Geld wir f\xFCr unser Handy ausgeben wollen, oder beim Ausgehen nur einen bestimmten Geldbetrag mitnehmen und die Bankomatkarte zu Hause lassen.</p>\n\n  <p><strong>Wenn du die Videos angeschaut hast, dann klicke bitte auf "Weiter" um ein Los daf\xFCr zu erhalten.</strong></p>\n\n  <div data-locked-state="selfCommitmentTaskController.isLocked()">\n    <!-- use default text -->\n  </div>\n\n  <button\n    class="uk-button uk-button-success"\n    data-ng-click="selfCommitmentTaskController.resolve()"\n    data-ng-disabled="!selfCommitmentTaskController.canResolve()">\n    <span>Weiter</span>\n  </button>\n</div>\n');
